"use client";

import ChatSupportDialog from "./ChatSupportDialog";

export default function ChatSupportPage() {
  return (
    <div className="min-h-screen bg-gray-50">
      <h1 className="text-4xl font-bold text-center py-10">Chat Support</h1>
      {/* Content for the page */}
      <div className="max-w-4xl mx-auto text-center p-6">
        <p className="text-lg text-gray-700">
          Need help? Our chat support is here to assist you. Start a conversation and get assistance right away!
        </p>
      </div>

      {/* Chat Support Dialog */}
      <ChatSupportDialog />
    </div>
  );
}

